.intro {
  margin-top: 60px;
  @include r(767) {
    margin-top: 25px; }
  h4 {
    font-size: 30px;
    font-weight: 500;
    letter-spacing: 0.8px;
    color: $charcoal-grey-three;
    @include r(1024) {
      font-size: 22px; }
    @include r(767) {
      font-size: 20px; } }
  h2 {
    font-weight: bold;
    font-size: 80px;
    letter-spacing: normal;
    color: $lipstick;
    text-transform: uppercase;
    @include r(1024) {
      font-size: 56px; }
    @include r(767) {
      font-size: 42px; } }
  h3 {
    font-size: 56px;
    color: $dark-two;
    letter-spacing: -0.6px;
    text-transform: uppercase;
    @include r(1024) {
      font-size: 32px; }
    @include r(767) {
      font-size: 24px; } }
  p {
    font-size: 15px;
    line-height: 1.47;
    letter-spacing: 0.4px;
    text-align: left;
    color: $charcoal-grey;
    max-width: 410px;
    margin-bottom: 50px;
    margin-top: 30px;
    @include r(767) {
      padding: 0 40px;
      font-size: 12px;
      text-align: center; } }
  .swiper-slide {
    background-repeat: no-repeat;
    background-size: cover;
    padding: 90px 0;
    @include r(767) {
      .container {
        padding: 0; } }
    img {
      max-width: 440px;
      width: 100%;
      @include r(767) {
        padding: 0 20px;
        padding-top: 50px; } } }
  .swiper-pagination {
    margin-bottom: 20px; } }
