.services {
  padding-bottom: 100px;
  &__block {
    border-top: 1px solid rgba($dark, 0.15);
    padding-top: 85px;
    padding-bottom: 45px;
    &:first-child {
      border: none; }
    .heading {
      margin: 0;
      margin-bottom: 35px;
      justify-content: flex-start; }
    p {
      font-size: 15px;
      line-height: 1.47;
      letter-spacing: 0.4px;
      color: $dark-three;
      padding-bottom: 20px;
      a {
        font-weight: 500;
        color: #df1f59; } }
    ul {
      margin-bottom: 20px; }
    li {
      font-size: 14px;
      line-height: 1.57;
      letter-spacing: 0.4px;
      color: $dark-three; } }
  .block__img {
    width: calc(100% - 20px); }
  &__phone {
    @include center;
    margin-bottom: 60px;
    a {
      font-size: 80px;
      letter-spacing: 2px;
      background-image: linear-gradient(345deg, #8016a7, #e01f59), linear-gradient($dark-three, $dark-three);
      background-clip: content-box;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; } } }
