.header {
  box-shadow: 0px 2px 12px 0 rgba(0, 0, 0, 0.1);
  &__toolbar {
    width: 100%;
    height: 50px;
    mix-blend-mode: undefined;
    background-color: $greyish-brown;
    display: flex;
    align-items: center;
    color: $white-six;
    @include r(767) {
      display: none; } }
  &__primary {
    padding: 20px; }
  &__mail {
    font-size: 15px;
    display: flex;
    align-items: center;
    font-size: 15px;
    letter-spacing: 0.9px;
    color: $white;
    font-weight: 300;
    @include r(1024) {
      justify-content: center; }
    i {
      margin-right: 9px; } }
  &__phone {
    display: inline-flex;
    align-items: center;
    margin-left: 50px;
    font-size: 18px;
    color: $white;
    text-decoration: none;
    i {
      margin-right: 10px; } }
  &__callback {
    width: 130px;
    height: 30px;
    text-align: center;
    text-decoration: none;
    background-image: linear-gradient(165deg, $tealish, $greenish-teal);
    border-radius: 15px;
    margin-left: 15px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 11px;
    font-weight: bold;
    color: $white; }
  &__social {
    @include r(1024) {
      display: none; }
    ul {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      a {
        color: rgba($white, 0.5);
        text-decoration: none;
        font-size: 15px;
        @include tr;
        &:not(:last-child) {
          margin-right: 20px; }
        &:hover {
          color: $white; } } } }
  .logo {
    width: 240px;
    @include r(1024) {
      width: 200px; }
    @include r(767) {
      width: 130px; }
    img {
      width: 100%; } }
  nav {
    flex: 1;
    padding-right: 50px;
    @include r(1124) {
      display: flex;
      justify-content: space-between; }
    @include r(767) {
      display: none; }
    .mobile {
      display: none;
      li {
        max-width: 180px;
        a {
          font-size: 14px; } }
      @include r(1124) {
        display: block !important; } }
    ul {
      display: flex;
      align-items: center;
      justify-content: space-between;
      @include r(1124) {
        display: none; }
      li {
        &.active {
          a {
            color: $lipstick-five;
            &:before {
              opacity: 1; } } } }
      a {
        font-size: 18px;
        color: $greyish-brown;
        position: relative;
        @include tr;
        &:before {
          content: '';
          width: 100%;
          height: 2px;
          background: $lipstick-five;
          position: absolute;
          bottom: -43px;
          opacity: 0;
          @include tr; }
        &:hover {
          color: $lipstick-five;
          &:before {
            opacity: 1; } } } } }
  &__primary__block {
    display: flex;
    justify-content: space-between;
    @include r(767) {
      justify-content: flex-end; } }
  &__tools {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100px;
    @include r(767) {
      width: 90px; }
    &__search {
      position: relative;
      i {
        color: $black-three; }
      &:before {
        content: '';
        width: 2px;
        height: 14px;
        background: rgba($black-three, 0.1);
        position: absolute;
        right: -25px;
        top: 6px;
        @include r(767) {
          right: -20px; } } }
    &__burger {
      width: 30px;
      height: 30px;
      display: inline-flex;
      position: relative;
      z-index: 999;
      &.active {
        span {
          background: transparent;
          &:before {
            width: 30px;
            transform: rotate(315deg);
            top: 0; }
          &:after {
            width: 30px;
            transform: rotate(-315deg);
            top: 0; } } }
      span {
        width: 15px;
        height: 2px;
        background: $black-three;
        display: block;
        position: relative;
        left: 15px;
        top: 15px;
        &:before {
          content: '';
          position: absolute;
          width: 27px;
          height: 2px;
          top: -10px;
          background: $black-three;
          right: 0;
          transition: all 0.5s; }
        &:after {
          content: '';
          position: absolute;
          top: 10px;
          width: 27px;
          height: 2px;
          background: $black-three;
          right: 0;
          transition: all 0.5s; } } } } }
